import React from "react";
import Uploader from "./Uploader";
import axios from "axios";
import { useState, useEffect } from "react";
import "./Upload.css";

export const Upload = () => {
  const URL =
    "https://zakfupe2q5.execute-api.ap-south-1.amazonaws.com/test/getallimages";
  const [images, getimages] = useState([]);
  const [currentImage, setCurrentImage] = useState("");
  const [upload, setUpload] = useState(false);
  const getAllImages = () => {
    axios
      .get(URL)
      .then((res) => {
        const data = res.data.urls;
        getimages(data);
      })
      .catch((err) => {
        console.error(err);
      });
  };
  useEffect(() => {
    getAllImages();
  }, []);
  return (
    <>
      <button className="upload-btn" onClick={() => setUpload(true)}>UPLOAD</button>
      {upload === true && (
        <div className="upload-overlay">
          <button className="cross-btn" onClick={() => setUpload(false)}>
            X
          </button>
          <Uploader setUpload = {setUpload}/>
        </div>
      )}
      {currentImage !== "" && (
        <div className="image-overlay">
          <button onClick={() => setCurrentImage("")}>X</button>
          <img className="uploads-selected-image" src={currentImage}></img>
        </div>
      )}

      <div className="uploads-main">
        <div className="uploads-images-cover">
          {images.map((image, index) => {
            return (
              <img
                onClick={() => setCurrentImage(image)}
                src={image}
                key={index}
              ></img>
            );
          })}
        </div>
      </div>
    </>
  );
};
