import React from "react";
import axios from "axios";
import { useState, useEffect } from "react";
import "./HomePage.css";
export const HomePage = () => {
  const URL =
    "https://zakfupe2q5.execute-api.ap-south-1.amazonaws.com/test/getallimages";
  const [images, getimages] = useState([]);
  const [currentImage, setCurrentImage] = useState("");
  const getAllImages = () => {
    axios
      .get(URL)
      .then((res) => {
        const data = res.data.urls;
        getimages(data);
      })
      .catch((err) => {
        console.error(err);
      });
  };
  useEffect(() => {
    getAllImages();
  }, []);

  return (
    <>
      {currentImage !== "" && (
        <div className="image-overlay">
          <button onClick={() => setCurrentImage("")}>X</button>
          <img className="home-selected-image" src={currentImage}></img>
        </div>
      )}

      <div className="home-main">
        <div className="home-images-cover">
          {images.map((image, index) => {
            return (
              <img
                onClick={() => setCurrentImage(image)}
                src={image}
                key={index}
              ></img>
            );
          })}
        </div>
      </div>
    </>
  );
};
// get all : https://zakfupe2q5.execute-api.ap-south-1.amazonaws.com/test/getallimages
