import React, { useState } from "react";
import "./Upload.css";
import axios from "axios";
const Uploader = (props) => {
  const [image, setImage] = useState();
  const API_ENDPOINT =
    "https://i5wpw4jmb8.execute-api.ap-south-1.amazonaws.com/default/getPresignedimageURL";
  const handleFileUpload = async (e) => {
    const file = e.target.files[0];
    setImage(file);
  };

  const uploadImage = async () => {
    const response = await axios({ method: "GET", url: API_ENDPOINT });
    await axios(response.data.uploadURL, {
      method: "PUT",
      headers: { "Content-Type": "image/jpeg" },
      data: image,
    }).then(()=>{
      props.setUpload(false);
      //alert("uploaded succesfully");
    }).catch((err)=>{
      alert("Upload failed");
    });
  };
  const onSubmit = (event) => {
    event.preventDefault();
    uploadImage();
  };

  return (
    <div className="uploader-main">
      <form className="uploader-form" onSubmit={onSubmit}>
        <input
          type="file"
          label="Image"
          name="myimage"
          accept=".jpeg, .png, .jpg"
          onChange={(e) => handleFileUpload(e)}
          required
        ></input>
        <button type="submit">UPLOAD</button>
      </form>
    </div>
  );
};

export default Uploader;
